import Vue from 'vue'
import loadErr from '@/components/loadErr.vue'
import noMore from '@/components/noMore.vue'
import { mapGetters , mapActions } from 'vuex';

export default {
	data() {
		return {
			startY: 0,
			isScroll: false,
			scrollTop: 0,
			scrollVal: 0, //滚动值
			sysInfo: null, //系统值
			slideVal: 0, //滑动值
			
			// index: 1,
			// size: 10,
			keywords: '', //
			dataList: [],//数据列表
			isMore: true,
			isLoadErr: false,
			userInfo: null, //用户信息
			
			isAuth: false, //登录状态
		}
	},
	components: { loadErr, noMore },
	computed: {
		...mapGetters([
			'_isToOrderList'
		]),
		listH() { //搜索宽总高度100upx 导航高度 this.CustomBar
			let sys = uni.getSystemInfoSync()
			return  sys.windowHeight - sys.windowWidth / 750 * 100 - this.CustomBar + 'px'
		},
		headTop() {
			let sys = uni.getSystemInfoSync()
			return  sys.windowWidth / 750 * 10 + this.CustomBar + 'px'
		},
		searchTop() {
			return this.CustomBar + 'px'
		}
	},
	onLoad() {
		this.sysInfo = uni.getSystemInfoSync()
		if(!this.$userInfo) { //加载登录配置
			this.$api.userLogin((status) => {
				if(status == 1) {
					this.isAuth = true
					this.userInfo = this.$userInfo
				}
			})
		}else {
			this.isAuth = true
			this.userInfo = this.$userInfo
		}
		
		if(this.$isSharePage) {
			Vue.prototype.$isSharePage = false //恢复false
		}
		
		this.miniAppUpdate() //小程序出发更新
	},
	onShow() {
		// this.index = 1
		// this.isMore = true
		this.dataList = []
		this.scrollVal = 0
		this.keywords = ''
		
		if(this._isToOrderList) {
			this.toJumpOrderList(false)
			uni.navigateTo({
				url: '/pages/orderList/orderList'
			})
		}else {
			setTimeout(() => {
				this.getListData()
			},10)
		}
		
	},
	onHide() {
		this.keywords = ''
	},
	methods: {
		...mapActions([
			'toJumpOrderList'
		]),
		// toJumpPlaceStart() { //会场开幕
		// 	uni.navigateTo({
		// 		url: '/pages/placeStart/placeStart'
		// 	})
		// },
		toLookAlbum() { //查看相册
			uni.navigateTo({
				url: '/pages/openAlbum/openAlbum'
			})
		},
		toJumpInitName() { //到参与加入
			uni.navigateTo({
				url: '/pages/joinStart/joinStart'
			})
		},
		toJumpDetail(idx) { //报名详情
			const $this = this
			uni.navigateTo({
				url: `/pages/apply/apply?dear_id=${$this.dataList[idx].dear_id}`
			})
		},
		toSearchKeyword(e) {
			this.keywords = this.$api.trim(e.detail.value)
			this.dataList = []
			// this.index = 1
			this.getListData()
		},
		toRelaodData() {
			// this.index = 1
			this.isLoadErr = false
			// this.isMore = true
			this.dataList = []
			this.keywords = ''
			this.getListData()
		},
		// toLoadMore() {
		// 	if(this.isMore) {
		// 		this.getListData()
		// 	}
		// },
		getListData() { //获取列表数据
			uni.showLoading({
				title: '数据加载中',
				mask: true
			})
			this.$api.ajax({
				url: this.$config.clanList,
				data: {
					keywords: this.keywords
				}
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					let arr = res.data.data
					arr.map(v => {
						if(!v.background) v.background = '#FFDD71'
					})
					this.dataList = arr
					// if(arr.length < this.size) {
					// 	this.dataList = this.dataList.concat(arr)
					// 	this.isMore = false //没有更多了
					// }else {
					// 	this.dataList = this.dataList.concat(arr)
					// 	this.index++
					// }
					setTimeout(() => {
						uni.hideLoading();
					}, 200)
				}else {
					uni.hideLoading();
					this.isLoadErr = true
					this.$api.warnNotice(res.data.message)
				}
			})
			.catch(() => {
				uni.hideLoading();
				this.isLoadErr = true
				this.$api.warnNotice('列表加载失败')
			})
		},
		toJumpOrderDetail() {
			uni.navigateTo({
				url: '/pages/orderList/orderList'
			})
		},
		getUserInfo: function(e) { //按钮登录授权
		  if (e.detail.errMsg == 'getUserInfo:ok') {
		    uni.showLoading({
		      title: '授权登陆中...',
		      mask: true
		    })
		    const $this = this
		    this.$api.userLogin((status) => {
		      if (status == '1') {
		         uni.hideLoading()
		         uni.showToast({
		            title: '授权登录成功',
		            icon: 'success',
		            mask: true,
		            success: () => {
						$this.isAuth = true
						$this.userInfo = $this.$userInfo
		            }
		        })
		      }
		    })
		  }else {
		    this.$api.warnNotice('您已拒绝授权！')
		    this.isAuth = false
		  }
		},
		toScroll(e) {
			this.scrollVal = e.detail.scrollTop
		},
		touchstart(e) {
			this.startY = e.touches[0].pageY
		},
		touchmove(e) {
			if(!this.isScroll) {
				if(this.startY - e.touches[0].pageY > 50) { //向下滑动
					this.slideVal = - this.sysInfo.windowWidth / 750 * ( 500 -100 )
					this.isScroll = true
				}
			}else {
				if(this.scrollVal == 0) {
					if(e.touches[0].pageY - this.startY > 50) { //向下滑动
						this.slideVal = 0
						this.isScroll = false
						if(this.keywords != '') {
							this.keywords = ''
							this.dataList = []
							this.index = 1
							this.getListData()
						}
					}
				}else {
					return
				}
			}
		},
		toScrollTop() {
			this.slideVal = 0
			this.isScroll = false
			this.scrollTop = 0
			if(this.keywords != '') {
				this.keywords = ''
				this.dataList = []
				this.index = 1
				this.getListData()
			}
		},
		hexToRgb(hex) {
		    let result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex.split('#')[1]);
		    return result ? `rgba(${parseInt(result[1], 16)},${parseInt(result[2], 16)},${parseInt(result[3], 16)},0.2)`: null;
		},
		miniAppUpdate: function() {
			const $this = this
		  const updateManager = uni.getUpdateManager()
		  updateManager.onUpdateReady(function () {
		    uni.showModal({
		      title: '更新提示',
		      content: '监测到小程序版本有更新，是否重启应用？',
		      confirmColor: '#FFDA4C',
		      success: function (res) {
		        if (res.confirm) {
		          // 新的版本已经下载好，调用 applyUpdate 应用新版本并重启
		          updateManager.applyUpdate()
		        }
		      }
		    })
		  })
		  updateManager.onUpdateFailed(function () {
		    // 新版本下载失败
		    $this.$api.warnNotice('新版本更新失败')
		  })
		}
	},
	onShareAppMessage() {
		const $this = this
		return {
			title: $this.$config.defaultName,
			imageUrl: $this.$config.defaultUrl
		}
	}
}
